.App {
  text-align: center;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: #e9ebee;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

#map {
  height: 300px;
  width: 100%;
}

.grid {
  margin-left: 1%;
  margin-right: 1%;
  margin-bottom: 1%;
}

.grid:after {
  content: "";
  display: table;
  clear: both;
}

*, *:after, *:before {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.sticky {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
}
.sticky-grid-heading {
  position: fixed;
  top: 300px;
  width: 98%;
}

.col-1-2 {
  float: left;
  width: 46%;
  margin-left: 2%;
  margin-right: 2%;
  border-bottom: 1px solid #222;
  background-color: #e9ebee;
  height: 60px;
}

.col-2-2 {
  float: left;
  width: 48%;
  margin-left: 1%;
  margin-right: 1%;
}


.yelp-places {
  margin-left: 2%;
  margin-right: 2%;
  margin-bottom: 2%;
  margin-top: 1%;
  border-bottom: 1px solid #222;
  height: 230px;
}
.google-places {
  margin-left: 2%;
  margin-right: 2%;
  margin-bottom: 2%;
  margin-top: 1%;
  border-bottom: 1px solid #222;
  height: 230px;
}

input {
  display: block;
  margin: 0 auto;
  margin-bottom: 7px;
  background-color: #e9ebee;
  border:0px solid #e9ebee;
  padding: 3px;
  width: 185px;
  outline-width: 0;
}

.loader-activated {
  display: block;
    /*position:fixed;top:0;right:0;bottom:0;left:0;*/
    /*background: #ff0000 url(...) no-repeat 50% 50%;*/
}

#loader {
  position: absolute;
  left: 56%;
  top: 50%;
  z-index: 1;
  width: 150px;
  height: 150px;
  margin: -75px 0 0 -75px;
  border: 16px solid #f3f3f3;
  border-radius: 50%;
  border-top: 16px solid #3498db;
  width: 120px;
  height: 120px;
  -webkit-animation: spin 2s linear infinite;
          animation: spin 2s linear infinite;
}

@-webkit-keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



